.galary {
  position: relative;
  margin-bottom: 200px;
  background: url(./../img/galary-bg.jpg) no-repeat center / cover;
  @media (max-width: $breakpoint576) {
    margin-bottom: 100px;
  }

  &:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: linear-gradient(
      109.8deg,
      rgba(0, 0, 0, 0.75) 0%,
      rgba(0, 0, 0, 0.9) 100%
    );
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 77px;
    @media (max-width: $breakpoint992) {
      flex-direction: column;
    }
    @media (max-width: $breakpoint576) {
      gap: 30px;
    }
  }

  &__item {
  }

  &__left {
    padding-top: 137px;
    position: relative;
    @media (max-width: $breakpoint992) {
      padding-top: 77px;
    }
  }

  &__title {
    font-family: "Yeseva One";
    font-size: 27px;
    line-height: 1.37;
    text-transform: uppercase;
    color: $accent-color;
    margin-bottom: 47px;
    max-width: 300px;
    @media (max-width: $breakpoint992) {
      margin: 0 0 40px;
    }
  }

  &__tabs {
  }

  &__nav {
    white-space: nowrap;
    display: flex;
    flex-direction: column;
    gap: 25px;
    font-size: 20px;
    line-height: 1.9;
    color: $white;
    @media (max-width: $breakpoint992) {
      margin-bottom: 40px;
      flex-direction: row;
    }
    @media (max-width: $breakpoint576) {
      overflow-x: auto;
      padding-bottom: 20px;
    }
  }

  &__tab-item {
    transition: 0.3s ease-in;
    button {
      position: relative;
      &:before {
        content: "";
        width: 0%;
        background: $accent-color;
        position: absolute;
        bottom: -2px;
        height: 1px;
        transition: 0.3s ease-in-out;
      }
    }
    &.active button:before {
      color: $accent-color;
      width: 100%;
    }
    &.active {
      color: $accent-color;
    }
  }

  &__right {
    position: relative;
    width: 750px;
    &::before {
      content: "";
      display: block;
      padding-top: calc(100% * (864 / 750));
    }
    @media (max-width: $breakpoint992) {
      // &::before {
      //   content: "";
      //   display: block;
      //   padding-top: calc(100% * (590 / 750));
      // }
    }
    @media (max-width: $breakpoint1200) {
      width: 650px;
    }
    @media (max-width: $breakpoint768) {
      width: 100%;
    }
  }

  &__content {
  }

  &__content-item {
    visibility: hidden;
    opacity: 0;
    transition: 1.5s ease;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateX(300%);

    &.active {
      transform: translateX(0);
      visibility: visible;
      opacity: 1;
    }
  }

  &__content-box {
    display: flex;
    gap: 40px;
    margin: -40px 0;
    min-width: 300px;
    @media (max-width: $breakpoint768) {
      gap: 28px;
    }
    @media (max-width: $breakpoint576) {
      gap: 10px;
    }
  }

  &__content-col {
    display: flex;
    flex-direction: column;
    gap: 40px;
    width: 354px;
    &:last-child {
      margin-top: -72px;
    }
    @media (max-width: $breakpoint1200) {
      &:last-child {
        margin-top: -52px;
      }
    }
    @media (max-width: $breakpoint992) {
      &:last-child {
        margin-top: 0;
      }
    }
    @media (max-width: $breakpoint768) {
      gap: 28px;
    }
    @media (max-width: $breakpoint576) {
      gap: 10px;
    }
  }
  &__content-img {
    object-fit: cover;

    @media (max-width: $breakpoint992) {
      max-height: 300px;
    }
  }
}

.active {
}
