.measurer {
  padding-bottom: 240px;
  @media (max-width: $breakpoint992) {
    padding-bottom: 140px;
  }
  @media (max-width: $breakpoint576) {
    padding-bottom: 90px;
  }
  @media (max-width: 450px) {
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    @media (max-width: $breakpoint992) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__col {
    @media (max-width: $breakpoint992) {
      width: 70%;
      &:not(:last-child) {
        margin-bottom: 70px;
      }
    }
    @media (max-width: $breakpoint768) {
      width: 100%;
    }
  }

  &__left {
    padding: 0 75px 54px 215px;
    background: url(./../img/measurer1.png) no-repeat left center / cover;
    @media (max-width: $breakpoint1200) {
      padding: 0 45px 24px 115px;
    }
    @media (max-width: $breakpoint576) {
      padding: 0 10px 24px 88px;
    }
  }

  &__list {
    font-family: "Nunito Sans";
    font-size: 14px;
    line-height: 1.35;
    display: flex;
    flex-direction: column;
    gap: 105px;
    max-width: 370px;
    counter-reset: quick-start;
    color: #363636;
  }

  &__item {
    position: relative;
    &:before {
      counter-increment: quick-start;
      content: counter(quick-start);
      width: 50px;
      height: 50px;
      background: $accent-color;
      border: 8px solid $white;
      box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.15);
      border-radius: 30px;
      position: absolute;
      left: -58px;
      top: 50%;
      transform: translateY(-50%);
      text-align: center;
      font-family: "Yeseva One";
      font-size: 18px;
      line-height: 33px;
      color: $white;
    }
  }

  &__right {
    background: #131313;
    box-shadow: 0px 30px 30px rgba(0, 0, 0, 0.05);
    padding: 28px 50px;
    position: relative;
    @media (max-width: 450px) {
      padding: 28px 0px;
    }
    @media (max-width: $breakpoint576) {
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: -18px;
        right: -18px;
        height: 100%;
        background: #131313;
        z-index: -1;
      }
    }
  }

  &__title {
    max-width: 234px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 39px;
  }

  &__form {
    @media (max-width: 450px) {
      font-size: 14px;
      align-items: stretch;
    }
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .form__box {
      width: 100%;
      &:not(:last-of-type) {
        margin-bottom: 59px;
      }
    }

    .form__input {
      width: 100%;
      color: $white;

      border-bottom: 1px solid rgba(255, 255, 255, 0.15);
    }
    .form__label {
      color: rgba(255, 255, 255, 0.5);
    }
    .form__check-box {
      margin-top: 19px;
      color: $white;
    }
    .form__btn {
      margin: 57px 0 0 auto;
      order: 4;
      @media (max-width: $breakpoint576) {
        margin: 30px 0 0 0;
      }
    }
  }
  &__save {
    font-size: 18px;
    line-height: 1.44;
    color: $accent-color;
    margin: 50px 0 0 auto;
    max-width: 344px;
    text-shadow: 0px 3px 10px rgba(227, 184, 115, 0.15);
    position: relative;
    &:before {
      content: "";
      background: $accent-color url(./../img/measurer2.svg) no-repeat center;
      width: 72px;
      height: 72px;
      border-radius: 50%;
      position: absolute;
      left: -99px;
      top: 50%;
      transform: translateY(-50%);
    }
    @media (max-width: $breakpoint576) {
      display: none;
    }
  }
}
