.btn {
  background: $accent-color;
  box-shadow: 0px 5px 15px rgba(227, 184, 115, 0.2);
  border-radius: 2px;
  padding: 17px 30px;
  line-height: 15px;
  font-size: 12px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: 0.2s ease-in;
  cursor: pointer;
  &:hover {
    background-color: $hover-btn;
  }
}

.hide {
  display: none;
}

.title {
  font-family: "Yeseva One";
  font-size: 20px;
  line-height: 1.4;
  text-align: center;
  text-transform: uppercase;

  &__text {
    margin-bottom: 11px;
  }
  &__img-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    &::before,
    &::after {
      content: "";
      width: 32px;
      height: 2px;
      border-radius: 30px;
      background-color: $accent-color;
    }
    &::before {
      margin-right: 9px;
    }
    &::after {
      margin-left: 9px;
    }
  }
}

.form {
  &__box {
    position: relative;
  }

  &__input {
    border-bottom: 1px solid rgba(54, 54, 54, 0.3019607843);
    width: 231px;
  }

  &__label {
    position: absolute;
    pointer-events: none;
    top: 0;
    left: 0;
    white-space: nowrap;
    overflow: hidden;
    transition: 0.2s;
    color: rgba(54, 54, 54, 0.5);
    transform-origin: top left;
  }
  .form__input:focus ~ .form__label,
  .form__input:not(:placeholder-shown) ~ .form__label {
    transform: scale(0.75) translateY(-30px);
  }
  &__check-box {
    color: $title;
  }
  &__check {
    clip: rect(1px 1px 1px 1px);
    border: 0;
    -webkit-clip-path: inset(50%);
    clip-path: inset(50%);
    height: 1px;
    margin: 0;
    overflow: hidden;
    padding: 0;
    position: absolute !important;
    white-space: nowrap;
    width: 1px;
    &:checked + .form__new-check::before {
      background: $accent-color;
    }
    &:checked + .form__new-check::after {
      display: block;
    }
  }
  &__new-check {
    position: relative;
    display: flex;
    align-items: center;
    &::before {
      margin-right: 9px;
      content: "";
      width: 18px;
      height: 18px;
      border-radius: 1px;
      border: 2px solid #e3b873;
    }
    &::after {
      content: "";
      background: url(./../img/check.svg) no-repeat center/ cover;
      width: 10px;
      height: 10px;
      left: 4px;
      display: none;
      position: absolute;
    }
  }
  &__btn {
    min-width: 200px;
    color: $white;
    text-transform: uppercase;
  }
}
