.project {
  padding: 107px 0 90px;
  background-color: #131313;
  @media (max-width: $breakpoint1400) {
    padding-top: 70px;
  }
  @media (max-width: $breakpoint576) {
    padding: 36px 0 58px 0;
  }
  &__inner {
    display: flex;
    gap: 50px;
    padding: 0 43px;
    position: relative;
    @media (max-width: $breakpoint1400) {
      flex-direction: column;
      align-items: center;
    }
    @media (max-width: $breakpoint576) {
      gap: 36px;
      padding: 0px;
    }
  }

  &__left {
    max-width: 442px;
    margin-top: 45px;
    @media (max-width: $breakpoint1400) {
      text-align: center;
    }
  }

  &__title {
    font-family: "Yeseva One";
    font-size: 27px;
    line-height: 1.37;
    letter-spacing: -0.07em;
    text-transform: uppercase;
    max-width: 250px;
    color: #e3b873;
    margin-bottom: 34px;
    @media (max-width: $breakpoint1400) {
      text-align: center;
      max-width: none;
    }
    @media (max-width: $breakpoint576) {
      font-size: 22px;
    }
  }

  &__text {
    font-size: 14px;
    line-height: 1.92;
    margin-bottom: 30px;
    @media (max-width: $breakpoint576) {
      font-size: 11px;
    }
  }

  &__right {
    max-width: 679px;
    width: 100%;
    min-width: 280px;
  }

  &__wrapper {
  }

  &__slide {
  }
  &__btn {
    display: none;
    cursor: pointer;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;

    justify-content: center;
    align-items: center;
    background: #e3b873;
    border-radius: 50%;
    width: 36px;
    height: 36px;
    @media (max-width: $breakpoint992) {
      display: flex;
    }
    svg {
      width: 6px;
      height: 11px;
    }
  }

  &__btn-prev {
    left: 13px;
  }

  &__btn-next {
    right: 13px;
    svg {
      transform: rotate(180deg);
    }
  }
  &__pagination {
    display: flex;
    gap: 60px;
    margin-bottom: 53px;
    @media (max-width: $breakpoint992) {
      display: none;
    }
  }

  &__info-slider {
    position: absolute;
    top: 304px;
    left: 42px;
    max-width: 433px;
    width: 100%;

    @media (max-width: $breakpoint1400) {
      position: static;
      order: 3;
    }
  }

  &__info-swiper {
  }

  &__info-wrapper {
  }

  &__info-slide {
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
    @media (max-width: $breakpoint1400) {
      justify-content: space-between;
    }
    @media (max-width: $breakpoint576) {
      gap: 30px;
    }
  }

  &__info-slides {
    width: calc(50% - 30px);
  }

  &__info-title {
    font-family: "Yeseva One";
    font-size: 18px;
    line-height: 1.38;
    text-transform: uppercase;
    color: $accent-color;
    margin-bottom: 9px;
    @media (max-width: $breakpoint576) {
      font-size: 14px;
    }
  }

  &__info-text {
    font-size: 13px;
    line-height: 1.61;
    @media (max-width: $breakpoint576) {
      font-size: 11px;
    }
  }
}

.swiper-pagination-bullet {
  height: auto;
  width: auto;
  background: transparent;
  font-family: "Yeseva One";
  font-size: 14px;
  line-height: 19px;
  text-transform: uppercase;
  opacity: inherit;

  color: rgba(255, 255, 255, 0.3);
}
.swiper-pagination-bullet-active {
  color: #e3b873;
  position: relative;
  &::before {
    content: "";
    background: #e3b873;
    height: 1px;
    width: 100%;
    position: absolute;
    bottom: -6px;
  }
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0;
}

.swiper-button-disabled {
  background: $hover-btn;
}
