.home {
  padding-top: 83px;
  background: url(./../img/hero_1.jpg) no-repeat center/cover;
  position: relative;
  &:before {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    content: "";
    background: rgba(0, 0, 0, 0.7);
  }

  &__wrapper {
    height: calc(100vh - 83px);
    display: flex;
    align-items: center;
    justify-content: center;
    @media (max-width: $breakpoint576) {
      align-items: flex-end;
    }
  }
  &__content {
    position: relative;
    z-index: 2;
    margin-left: 43px;
    max-width: 519px;
    @media (max-width: $breakpoint768) {
      margin-left: 33px;
      max-width: 450px;
    }
    @media (max-width: $breakpoint576) {
      margin: 0 0 83px 0;
    }
  }

  &__title {
    font-family: "Yeseva One";
    font-size: 40px;
    line-height: 1.32;
    text-transform: uppercase;
    color: $white;
    margin-bottom: 11px;
    span {
      color: $accent-color;
    }
    @media (max-width: $breakpoint992) {
      font-size: 33px;
    }
    @media (max-width: $breakpoint768) {
      font-size: 27px;
      line-height: 1.32;
    }
    @media (max-width: $breakpoint576) {
      font-size: 22px;
      line-height: 1.32;
    }
  }

  &__text {
    font-size: 22px;
    line-height: 1.63;
    margin-bottom: 25px;
    @media (max-width: $breakpoint992) {
      font-size: 20px;
    }
    @media (max-width: $breakpoint768) {
      font-size: 18px;
      line-height: 1.63;
    }
    @media (max-width: $breakpoint576) {
      font-size: 14px;
      line-height: 1.63;
    }
  }

  &__box-btn {
    display: flex;
    gap: 22px;
    @media (max-width: 500px) {
      flex-direction: column;
    }
  }

  &__button-calc {
  }

  &__button-submit {
    border: 1px solid $white;
    box-shadow: none;
    background: transparent;
  }
}
