.control {
  padding-bottom: 147px;
  background: linear-gradient(0deg, #fff 60%, #131313 0);
  @media (max-width: $breakpoint768) {
    background: linear-gradient(0deg, #fff 8%, #131313 0);
    padding-bottom: 58px;
  }
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin-bottom: 31px;
  }

  &__video-wrap {
    width: 100%;
    max-width: 817px;
    min-width: 320px;
    margin-bottom: 100px;
    @media (max-width: $breakpoint768) {
      margin-bottom: 70px;
    }
  }

  &__video {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    width: 100%;
  }

  &__video iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
  }

  &__text {
    position: relative;
    font-size: 18px;
    line-height: 2;
    text-align: center;
    color: #363636;
    max-width: 680px;
    margin-bottom: 90px;
    @media (max-width: $breakpoint768) {
      color: $white;
      margin-bottom: 61px;
      font-size: 16px;
    }
    @media (max-width: $breakpoint576) {
      font-size: 11px;
    }
    &:before {
      content: "";
      position: absolute;
      top: -34px;
      left: 50%;
      transform: translateX(-50%);
      width: 55px;
      height: 2px;
      background: $accent-color;
      background-repeat: 30px;
    }
  }

  &__form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    max-width: 770px;
    @media (max-width: $breakpoint768) {
      flex-direction: column;
      justify-content: space-between;
    }
    @media (max-width: $breakpoint576) {
      max-width: 100%;
      width: 100%;
    }
    @media (max-width: 450px) {
      align-items: stretch;
      font-size: 14px;
    }
    .form__box {
      @media (max-width: $breakpoint768) {
        &:first-child {
          margin-bottom: 63px;
        }
      }
      @media (max-width: 450px) {
        width: 100%;
      }
    }
    .form__label {
      @media (max-width: $breakpoint768) {
        color: $white;
      }
    }
    .form__check-box {
      margin: 40px auto 0;
      @media (max-width: $breakpoint768) {
        color: $white;
      }
      @media (max-width: 450px) {
        margin: 40px 0 0 0;
      }
    }
    .form__input {
      @media (max-width: $breakpoint768) {
        border-bottom: 1px solid $white;
      }
    }
    .form__btn {
      @media (max-width: $breakpoint768) {
        order: 3;
        margin-top: 63px;
      }
      @media (max-width: $breakpoint576) {
        order: 3;
        margin-top: 43px;
      }
    }
  }
}

.just-validate-error-field {
  border-bottom: 1px solid red;
}
.just-validate-error-label {
  display: none;
}
