.clients {
  color: $title;
  padding-bottom: 350px;
  &__title {
    margin-bottom: 25px;
  }

  &__text {
    font-size: 18px;
    line-height: 1.61;
    text-align: center;
    color: #363636;
    margin-bottom: 31px;
  }

  &__inner {
    display: flex;
    gap: 54px;
    @media (max-width: $breakpoint992) {
      flex-wrap: wrap;
      justify-content: center;
    }
    @media (max-width: $breakpoint768) {
      gap: 70px;
    }
  }

  &__item {
    padding: 20px 20px 67px;
    position: relative;
    width: 33.3333%;
    box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.04);
    @media (max-width: $breakpoint768) {
      width: 100%;
    }
  }

  &__user {
    display: flex;
    gap: 23px;
    margin-bottom: 15px;
    align-items: center;
  }

  &__user-img {
    width: 76px;
    height: 76px;
    border-radius: 50%;

    object-fit: cover;
  }

  &__user-title {
    font-family: "Yeseva One";
    font-size: 18px;
    line-height: 1.27;
    color: $title;
  }

  &__descr {
    font-size: 13px;
    line-height: 180%;
    color: #959595;
    margin-bottom: auto;
  }

  &__widgets {
    position: absolute;
    left: 0;
    bottom: -27px;
    display: flex;
    background: $accent-color;
    padding: 15px 0;
    display: flex;
    font-family: "Yeseva One";
    font-size: 17px;
    line-height: 1.41;
    text-transform: uppercase;
    color: $white;
    @media (max-width: $breakpoint768) {
      font-size: 15px;
    }
  }

  &__widget {
    padding-left: 15px;
    display: flex;
    align-items: center;
    gap: 15px;
    &:first-child {
      border-right: 0.75px solid #ffffff;
    }
  }

  &__widget-img {
  }

  &__widget-text {
    margin-right: 15px;
  }
}
