.location {
  background: no-repeat center / cover;
  position: relative;
  z-index: 2;
  &:before {
    content: "";
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      180deg,
      rgba(24, 24, 24, 0.9) 3.47%,
      rgba(24, 24, 24, 0.72) 100%
    );
    @media (max-width: $breakpoint576) {
      background: $title;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__form {
    margin-top: -212px;
    background: #c4c4c4;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: $title;
    padding: 34px 181px;
    width: 100%;
    max-width: 883px;
    margin-bottom: 52px;
    font-size: 14px;
    position: relative;
    @media (max-width: $breakpoint992) {
      max-width: 80%;
      padding: 34px 101px;
    }
    @media (max-width: $breakpoint768) {
      max-width: 100%;
    }
    @media (max-width: $breakpoint576) {
      padding: 28px 0px;
      margin-bottom: 32px;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background: red;
        right: -18px;
        left: -18px;
        z-index: -1;
        height: 100%;
        background: $title;
      }
    }
    @media (max-width: 450px) {
      align-items: stretch;
    }

    .form__label {
      color: rgba(255, 255, 255, 0.5);
    }

    .form__input {
      color: $white;
      border-bottom: 1px solid rgba(255, 255, 255, 0.15);
      width: 100%;
    }

    .form__box {
      width: 100%;
    }

    .form__check-box {
      margin-top: 46px;
      color: $white;
    }

    .form__btn {
      margin-top: 50px;
    }
    .form__new-check {
    }

    &-title {
      font-family: "Yeseva One";
      font-size: 20px;
      line-height: 1.4;
      text-align: center;
      text-transform: uppercase;
      color: $white;
      margin-bottom: 45px;
      @media (max-width: $breakpoint576) {
        font-size: 16px;
      }
    }
  }
  &__form-wrap {
    width: 100%;
    display: flex;
    margin-bottom: 48px;
    gap: 50px;
    font-size: 14px;
    line-height: 1.64;
    color: $white;
    @media (max-width: $breakpoint768) {
      flex-wrap: wrap;
    }
  }

  &__title {
    font-family: "Yeseva One";
    font-size: 20px;
    line-height: 1.4;
    text-align: center;
    text-transform: uppercase;
    color: $accent-color;
    margin-bottom: 26px;
    max-width: 590px;
    @media (max-width: $breakpoint576) {
      font-size: 16px;
    }
  }

  &__soc {
    display: flex;
    gap: 15px;
    margin-bottom: 40px;
  }

  &__soc-item {
    background: $accent-color;
    width: 28px;
    height: 28px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    svg {
    }
  }

  &__map {
    width: 100%;
    height: 100%;
    max-width: 925px;
    margin-bottom: 65px;
  }

  &__wrap {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
  }

  &__wrap-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

