.repair {
  padding-bottom: 454px;
  @media (max-width: $breakpoint576) {
    padding-bottom: 320px;
  }

  &__title {
    margin-bottom: 31px;
    color: $title;
  }

  &__inner {
    display: flex;
    align-items: center;
    gap: 30px;
    @media (max-width: $breakpoint1200) {
      flex-direction: column;
      gap: 284px;
    }
    @media (max-width: $breakpoint768) {
      padding: 0;
    }
    @media (max-width: $breakpoint576) {
      gap: 244px;
    }
  }

  &__item {
    width: calc(33.33%);
    background-color: red;
    background: no-repeat center / cover;
    height: 421px;
    @media (max-width: $breakpoint1200) {
      width: 70%;
    }
    @media (max-width: $breakpoint768) {
      width: 100%;
    }
  }

  &__wrap {
    position: relative;
    top: 224px;
    color: #363636;
    padding: 25px 35px 49px 35px;
    background: $white;
    margin: 0 30px;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.03);
    @media (max-width: $breakpoint1200) {
      margin: 0 60px;
    }

    @media (max-width: $breakpoint768) {
      margin: 0 90px;
    }

    @media (max-width: $breakpoint576) {
      margin: 0 60px 0 0;
      padding: 20px 30px 49px 10px;
    }
  }

  &__subtitle {
    color: #363636;
    font-family: "Yeseva One";
    font-size: 18px;
    line-height: 1.38;
    margin-bottom: 13px;
  }

  &__list {
  }

  &__list-item {
    position: relative;
    font-size: 13px;
    line-height: 2.07;
    color: #959595;
    margin-left: 11px;
    & + &:not(:first-child) {
      margin-top: 11px;
    }
    &:before {
      content: "";
      position: absolute;
      left: -11px;
      top: 9px;
      width: 5px;
      height: 5px;
      background: #959595;
      border-radius: 50%;
    }
    @media (max-width: $breakpoint576) {
      & + &:not(:first-child) {
        margin-top: 6px;
      }
    }
  }

  &__btn {
    position: absolute;
    left: 0;
    bottom: -26px;
    font-family: "Yeseva One";
    font-size: 18px;
    line-height: 1.38;
    text-transform: uppercase;
    color: #ffffff;
    @media (max-width: $breakpoint576) {
      font-size: 15px;
      line-height: 138.6%;
    }
  }
}
